.login-card{
     padding: 20px;
    width:520px;
    height: 800px;
    border-radius: 20px !important;
    text-align: center;
    background-color: lightgray !important;
}
#login-button{
    color:gold !important;
    background-color: rgb(100, 100, 241) !important;
    width:60% !important;
    margin:auto;
    font-size: 16px !important;
    text-transform: capitalize !important;
}
#login-button:hover{
    background-color: rgb(80, 80, 241) !important;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}
.login-card div{
    margin-top:8px;
}
#body{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
#body input{
    border-radius: 15px;
    border:none;
    background-color:white;
}
#body div{
    margin-top:10px;
    margin-bottom:10px;
    border-radius: 15px;
}
#header{
  display: flex;
  justify-content:center!important;
}
#header div{
    width:100px !important;
    height:100px !important;
}
#footer{}
#footer button{
    margin-top:20px;
    width:100%;
}
#auth-buttons{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
}
#auth-buttons button , img{
  width:50px;
  height:50px;
}
#auth-buttons button{
   margin:auto;
    background-color:white;
}
#footer .buttons-login{
    background-color:#7B7B7B;
}