#chat-inference-option-card {
    margin-top:20px;
    padding:20px;
    background: lightgray;
    width:60%;
    border-radius:20px;
}
#settings-page{
    height:900px!important;
    width:100% !important;
    overflow-y:scroll;
}
#settings-page::-webkit-scrollbar {
    width: 12px; /* حدد عرض شريط التمرير */
}

#settings-page::-webkit-scrollbar-thumb {
    background: #5454b6;
    border-radius: 6px;
}

#chat-inference-option-card div {
    display: flex;
    gap: 10px;
}
#pick-LLMs-card{
    margin-top:20px;
    padding:20px;
    background: lightgray;
    width:60%;
    border-radius:20px;
}
.radio-group div {
    display: flex;
    gap: 10px;
}
#pick-LLMs-card div{
    border-radius:10px;
}
#pick-LLMs-card div #expandIcon{
    width:40px;
    height:40px;
}
#radio-group div input{
    width:65px;
    height:65px;
}
#temperature-card{
    margin-top:20px;
    padding:20px;
    background: lightgray;
    width:60%;
    border-radius:20px;

}
.card{
    margin: auto;
    margin-top:20px !important;
    padding:20px !important;
    background: lightgray !important;
    width:60% !important;
    border-radius:20px !important;
}
