#conversations-container{
    /*width:260px;*/
    height:170px;
    background-color:  #f5f5f5;
    margin-bottom:30px;

}
.conversations-list::-webkit-scrollbar {
    width: 12px; /* حدد عرض شريط التمرير */
}

.conversations-list::-webkit-scrollbar-thumb {
    background: #5454b6;
    border-radius: 6px;
}

#menu-items{
    width:260px;
    background-color: #f5f5f5;
    overflow-x:hidden;
    display:flex !important;
    flex-direction:column !important;
    align-items:center !important;
    height:100vh;
}


#dashboard{height:100%;
    overflow-x:hidden;
    display:flex;
}
#appbar{
    display:flex;
    width:95%;
    height:100%;
    margin-right:0;
    overflow-x:hidden;

}
#drawer{
    overflow-x:hidden;
    flex-grow:0;
}
#appbar{
    display:flex;
}
#list-items{
    display:flex;
    flex-direction:column;
    align-items:center;
    width:100%;
    height:100%;
    overflow-x:hidden;


}
#menu-list{
    width:100%;
    height:100%;
}
#list-items .menu-item{

    width:fit-content;



}
.list-buttons{
    display:grid !important;
    border-radius:4px !important;
    background-color:gray !important;
    grid-template-columns: 1fr 3fr;
    gap:3px!important;
}
#dashboard-container{
    width:100%;
    height:80%;


    padding:0 !important;
    display:flex!important;
    justify-content:center!important;
    flex-direction: column !important;
}
#dashboard-container::-webkit-scrollbar {
    width: 12px; /* حدد عرض شريط التمرير */
}

#dashboard-container::-webkit-scrollbar-thumb {
    background: #5454b6;
    border-radius: 6px;
}
#dashboard{

    overflow-y:hidden;
    position:relative;
    top:64px;
}
#logout-button{
    position:relative   !important;
    top:400px!important;
    border-radius:10px!important;
    color:white;
    background: #c82d2d !important;
}
