#conversation-card {
    display: flex;
    justify-content: space-between;
    padding:20px;
    margin-top:10px;
    border-radius:15px;
    border:2px solid #ddd;
    background-color:lightgray ;
    transition: all 300ms;
}
#conversation-card:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transform: scale(1.02);
}
#deleteButton{
    width:50px ;
    color:white;
    height:50px;
    background-color:red;
    transition: all 300ms;
}
#deleteButton:hover{
     box-shadow: 0 4px 8px rgba(241, 6, 6, 0.795);
    transform: scale(1.02);
}
#title{
    width: 90%;
    padding-top:10px;
    padding-bottom:10px;
    height:100%;
    cursor:pointer;
    display:flex;
    gap:10px;
}
#deleteAll{

    font-size:20px;
    background-color:red;
}
#container-history{
    height:1000px;
    
}