.chat-field-container{
    width: 100%;
    max-height:65%;
    display: flex;
    overflow-x: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end; /* اجعل التمدد للأعلى عبر رسو المكوّن أسفل الحاوية */
    position: sticky;
    bottom: 0;
    background:#f5f6fa;
    padding-top: 4px;
}

/* حقل الكتابة: استجابة كاملة بدون ارتفاع ثابت */
.chat-field{
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: minmax(0, 1fr) auto; /* مهم: يمنع دفع الصف السفلي */
    grid-auto-rows: auto;
    height: auto;
    min-height: 120px;
    max-height: 50vh;
    width: min(960px, 96%);
    padding: 14px 16px;
    gap: 10px;
    box-shadow: 0 6px 18px rgba(0,0,0,0.12);
    border: 1px solid #dedede;
    overflow: hidden;
    border-radius:16px;
}

/* عمود النص/الصورة */
.first-row-first-column {
    grid-column: 1 / 2; /* كان 1 / -1: الآن يشغل العمود الأول فقط لضمان محاذاة زر الإرسال يميناً */
    grid-row: 1 / 2;
    display: flex;
    flex-direction: column;
    gap: 10px;
    /* لا تجعل وجود مساحة سفلية يرفع الأدوات */
    justify-content: flex-start;
    overflow-y: auto;
    min-height: 0;   /* مهم جداً للسماح بالانكماش داخل grid */
    max-height: 100%;
    flex: 1 1 auto;

}

/* صف الإدخال (معاينة + textarea) بشكل أفقي */
.input-row{
    display:flex;
    align-items: stretch; /* اسمح للـ textarea بالتمدد رأسياً ضمن الصف */
    gap: 12px;
    width: 100%;
}

/* عناصر داخلة لهذا العمود (نصوص عامة) */
.first-row-first-column div{
    width:100%;
    border:none;
    font-size:16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding:0;
}

/* عمود أزرار الإرسال/الإيقاف */
.first-row-second-column{
    grid-column: 2 / -1;
    grid-row: 1 / 2;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

/* صف الأدوات السفلي */
.first-column-second-row{
    grid-column: 1 / -1;
    grid-row: 2 / -1;
    position:static;
    display: flex;
    justify-content: space-between;
    align-items:center;
    gap: 8px;
    /* اختيارياً لتحسين الوضوح عند التمرير في الأعلى */
    background: rgba(255,255,255,0.9);

}

/* textarea ديناميكي الارتفاع مع التفافات */
#textarea-chat{
    width:100%;
    height:auto;
    min-height:56px;
    /* لا حاجة لحد أقصى صارم هنا؛ العمود نفسه يمرّج */
    max-height: 28vh;
    resize: none;
    border:none;
    outline:none;
    unicode-bidi: plaintext;
    font-size:16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding:14px 14px;
    box-sizing: border-box;
    overflow-y: auto;
    white-space: pre-wrap;
    word-break: break-word;
    flex: 1 1 auto;


}

/* معاينة الصورة المرفوعة: مرنة وواضحة */
#imageUploaded{
    width: clamp(120px, 25%, 220px);
    max-height: clamp(100px, 22vh, 200px);
    position:relative;
    bottom:0;
    border:1px solid #e0e0e0;
    border-radius:12px;
    background:#fafafa;
    overflow:hidden;
    align-self: flex-start;
    margin: 4px 0;
    flex: 0 0 auto;


}

#imageUploaded img{
    width:100%;
    height:auto;
    max-height: clamp(100px, 22vh, 200px);
    object-fit:contain;
    display:block;

}

/* اجعل الـ textarea تتمدد داخل الصف الأفقي */
.input-row #textarea-chat{
    flex: 1 1 auto;
}

/* زر إغلاق المعاينة */
.close-upload-btn{
    position:absolute !important;
    top:6px;
    right:6px;
    background:rgba(255,255,255,0.9);
    border:1px solid #ddd;
    width:28px;
    height:28px;
    padding:0px;
    border-radius:50%;
}

/* منطقة العرض */
.chat-area{
    height:75vh!important;
    width:100% !important;
    display:flex;
    border-bottom:none !important;
    box-shadow:0 0 0 white !important;
    flex-direction: column;
    align-items:center;
    overflow-y: scroll;
}

/* رسائل الذكاء الاصطناعي والمستخدم كما هي */
#ai-message{
  width:60%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding:10px
}
#ai-message div{
    unicode-bidi: plaintext;
    background-color:#e6f7e6 ;
    padding:8px;
    font-size:16px;
    max-width:90%;
    white-space:wrap;
    overflow-x:auto;
    text-align: left;
    border-radius:10px;
}
#user-message{
    display: flex;
    width:60%;
    justify-content: flex-end;
    padding:10px
}
#user-message div{
    unicode-bidi: plaintext;
    background-color:#e6f0ff ;
    border:none;
    font-size:16px;
    max-width:90%;
    text-align: right;
    padding:8px;
    border-radius:10px;
}
#user-image{
    object-fit:contain;
    border-radius:50%;
    margin-right:10px;
}

/* حاويات أخرى كما هي */
#user-image-container{
    display:flex;
    justify-content: flex-end;
    align-items: center;
}
#user-image-container{
   width:100%;
   height:500px ;
    display:none;
}
#user-image-container img{
    width:100%;
    height:100%;
    border-radius:10px;
}
#user-image-container video{
    width:100%;
    height:100%;
    border-radius:10px;
}

.chat-area::-webkit-scrollbar {
    width: 12px; /* حدد عرض شريط التمرير */
}
.chat-area::-webkit-scrollbar-thumb {
    background: #5454b6;
    border-radius: 6px;
}

#ai-image-container{
    width:100%;
    height:80% ;
    display:none;
}
#ai-image-container img{
    width:100%;
    height:100%;
    border-radius:10px;
}

/* زر رفع الملف مع سبينر تحميل */
.upload-button{
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}
.upload-button.loading button{
    opacity: 0.6;
    pointer-events: none;
}
.upload-spinner{
    position: absolute;
    right: -12px;
    top: -12px;
    width: 24px;
    height: 24px;
    border: 3px solid rgba(0,0,0,0.15);
    border-top-color: #1976d2; /* لون متناسق مع MUI primary */
    border-radius: 50%;
    animation: upload-spin 0.9s linear infinite;
    display: none;
}
.upload-button.loading .upload-spinner{
    display: inline-block;
}

@keyframes upload-spin{
    to { transform: rotate(360deg); }
}

/* استجابة للشاشات الصغيرة: اجعل الأزرار في سطر مستقل بالكامل */
@media (max-width: 600px){
    .chat-field{
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto;
    width: 96%;
    padding: 10px;
    }
    .first-row-first-column{
            grid-column: 1 / 2; /* كان 1 / -1: الآن يشغل العمود الأول فقط لضمان محاذاة زر الإرسال يميناً */
        grid-row: 1 / 2;
    }
    .first-row-second-column{
        grid-column: 1 / -1;
        grid-row: 2 / 3;
        align-items: flex-end;
        justify-content: center;
    }
    .first-column-second-row{
        grid-column: 1 / -1;
        grid-row: 3 / 4;
    }
}

/* New: input surface and hints */
.input-surface{
    width: 100%;
    border: 1px solid #e7e7e7;
    border-radius: 12px;
    background: #fff;
    box-shadow: inset 0 1px 0 rgba(0,0,0,0.02);
    padding: 4px 6px 6px;
}
.input-hints{
    color: #8a8a8a;
    font-size: 12px;
    padding: 0 8px 4px;
}

/* Floating send button styles */
.send-btn{
    background: #5454b6;
    color: #000 !important;
    box-shadow: 0 6px 16px rgba(84,84,182,0.3);
    width: 46px;
    height: 46px;
    border-radius: 12px !important;
    transition: transform 120ms ease, box-shadow 120ms ease, opacity 120ms ease;
}
.send-btn:hover{
    transform: translateY(-1px);
    box-shadow: 0 10px 22px rgba(84,84,182,0.35);
}
.send-btn:disabled{
    opacity: 0.5;
    box-shadow: none;
}
.send-btn.danger{
    background: #ef5350;
    box-shadow: 0 6px 16px rgba(239,83,80,0.28);
}
.image-container{
    width:400px;
    margin:auto;
    height:300px;
    display: none;
   
}